*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;  
}

.card p{
  padding: 5px;
  text-align: center;
  max-width: fit-content;
}

.card img{
  max-height: 150px;

}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}